<template>
	<div class="finding_wrap">
    
    <div class="finding_content">
      <div class="finding-header" style="position: relative;">
      <i class="el-icon-user-solid"></i><label id="finding-username">{{finding.UserNickname}}</label>
      <div style="z-index: 30; position: absolute; right: 30px; top: 0; height: 100%; width: auto; display: inline-block; outline: 1px solid #FF5B24; color: #FF5B24; padding: 5px; margin: 1px;"> 
        <p>Published</p>
      </div>
      </div>
      <div class="finding_block">
        <div class="finding-title">
          <p id="finding-name">{{finding.Name}}</p>
          <p id="finding-description">{{finding.Description}}</p>
        </div>
        <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(finding)">
        <div class="notes-section">
          <p id="finding-time">{{finding.TimeStamp}}</p>
          <p id="finding-note">{{finding.Notes}}</p>
        </div>
        <img v-if="datasetIndex!=0" class="clickable" src="@/assets/left-arrow.png" @click="setDatasetIndex(-1)" style="position: absolute; bottom: 45%; left: 35px; width: 70px; height: 70px; border-radius: 50px;"/>
        <img v-if="datasetIndex!=finding.Dataset2.length-1" class="clickable" src="@/assets/right-arrow.png" @click="setDatasetIndex(+1)" style="position: absolute; bottom: 45%; right: 35px; width: 70px; height: 70px; border-radius: 50px;"/>
        <!-- <div style="position: absolute; top: 580px; left: 25%; width: 50%; height: 100px; border-radius: 10px; font-size: 30px; font-weight: bold; background-color: #FFF3CF; opacity: 0.8; text-align: center;">
          <p style="font-size: 35px; font-weight: normal; margin: 30px;">{{finding.Dataset2[datasetIndex].Condition}}</p>
        </div> -->
      </div>
      <div id="finding_comment">
        <p>{{comments.length}} Comments</p>
        <div class="comment_row">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{username}}</span><br><br>
          <img src="@/assets/add-24-px@2x.png" v-bind:class="{'selected-comment':commentType==1}" class="clickable" @click="enableCommentInput(1)"/>
          <img src="@/assets/favorite-24-px@2x.png" v-bind:class="{'selected-comment':commentType==1, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(1)"/>
          <img src="@/assets/help-g-24-px@2x.png" v-bind:class="{'selected-comment':commentType==2, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(2)"/>
          <img src="@/assets/wb-incandescent-24-px@2x.png" v-bind:class="{'selected-comment':commentType==3, 'greyout':isCommentInputEnabled}" class="clickable" @click="enableCommentInput(3)"/>
          <input type="text " v-if="isCommentInputEnabled" v-model="commentValue"/>
          <button v-if="isCommentInputEnabled" @click="postComment()">Post</button>
          <button v-if="isCommentInputEnabled" @click="isCommentInputEnabled=false; commentType=0;">X</button>
        </div>
        <div class="comment_row" v-for="comment in comments" v-bind:key="comment.Id">
          <i style="color: black;" class="el-icon-user-solid"></i>
          <span class="comment-user">{{comment.UserName}}</span>
          <br><br>
          <span><img v-if="comment.Type==1" src="@/assets/favorite-24-px@2x.png" /></span>
          <span><img v-if="comment.Type==2" src="@/assets/help-g-24-px@2x.png"/></span>
          <span><img v-if="comment.Type==3" src="@/assets/wb-incandescent-24-px@2x.png"/></span>
          <span class="comment-value">{{comment.Value}}</span>
          <br>
        </div>
      </div>
    </div>
    <div class="middle-column"></div>
    <div class="finding_list">

      <!-- v-if="parentFinding.Base" -->
      <div class="replication-remix-tab-group" >
        <div class="tab-selection clickable" @click="selectedTab=1;" v-bind:class="{'selected-tab':selectedTab==1}">
          <span>{{replications.length}} Replications</span>
        </div>
        <div class="tab-selection clickable" @click="selectedTab=2;" v-bind:class="{'selected-tab':selectedTab==2}">
          <span>{{variations.length}} Remixes</span>
        </div>
      </div>

        <div class="empty-list-image-block" v-if="replications.length==0 && selectedTab==1">
          <img class="empty-list-image" src="@/assets/arabica-18@2x.png"/>
          <p v-if="finding.UserId==userId">Validate your results by replicating your inquiry</p>
          <p v-else>Be the first to replicate this experiment!</p>
        </div>

        <div class="empty-list-image-block" v-if="variations.length==0 && selectedTab==2">
          <img class="empty-list-image" src="@/assets/arabica-upgrade@2x.png"/>
          <p>No remixes yet</p>
        </div>

      <div v-if="selectedTab==1 && !finding.Base && replications.length>0">
        <div class="similar-block" style="margin-right: 8px;">
          <span class="similar-icon">😄</span>
          <span><label class="similar-value">{{similarResults}}</label></span>
          <br><span class="similar-label">Similar</span>
        </div>
        <div class="similar-block" style="margin-right: 8px;">
          <span class="similar-icon">🤔</span>
          <span><label class="similar-value">{{differentResults}}</label></span>
          <br><span class="similar-label">Different</span>
        </div>
        <div class="similar-block">
          <span class="similar-icon">🙃</span>
          <span><label class="similar-value">{{notsureResults}}</label></span>
          <br><span class="similar-label">Not sure</span>          
        </div>
      </div>

     
      <!-- v-if="!finding.Base"  -->
      <div class="replicate-remix-button" @click="replicateOrRemix()"> 
        {{selectedTab==1? 'Replicate': 'Remix'}} 
      </div>

      <div id="replications-block" v-if="selectedTab==1 && finding.Type==1" style="width:100%; height: 700px;">
        <el-card v-for="replication in replications" v-bind:key="replication.id" style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(replication.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(replication)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{replication.UserNickname}}</div>
            <div class="box-name">{{replication.Name}}</div>
          </div>
        </el-card>
      </div>
      <!-- <div id="original-finding-block" v-if="selectedTab==1 && parentFinding.Type==1" style="width:100%; height: 700px">
        <el-card style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(parentFinding.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(parentFinding)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{parentFinding.UserNickname}}</div>
            <div class="box-name">{{parentFinding.Name}}</div>
          </div>
        </el-card>
      </div> -->
      <div id="variations-block" v-if="selectedTab==2" style="width:100%; height: 700px">
        <el-card v-for="variation in variations" v-bind:key="variation.id" style="margin-bottom: 20px;" class="box-card default-card clickable" shadow="hover" :body-style="{ padding: '0px' }" @click.native="loadFinding(variation.Id)">
          <img v-bind:src="'https://kiwrious.org/Storage/'+ getMedia(variation)">
          <div class='box-description'>
            <div class="box-user"><i class="el-icon-user-solid"></i>{{variation.UserNickname}}</div>
            <div class="box-name">{{variation.Name}}</div>
          </div>
        </el-card>
        
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: 'https://kiwrious.org/api',
  })
  export default {
    data() {
      return {
        username:"",
        finding:{Name:"", Description:"", Dataset2:[{Media:""}], Type:1, Base:null, Status:2},
        parentFinding:{Name:"", Description:"", Dataset2:[{Media:""}], Type:0, Base:"_"},
        comments: [],
        replications: [],
        similarResults: 0,
        differentResults: 0,
        notsureResults: 0,
        variations: [],
        selectedTab: 1,
        isCommentInputEnabled: false,
        commentValue: "",
        commentType: 0,
        datasetIndex: 0,
        userId: ""
      }
    },
    props: ['id'],
    created() {
      const findingId = sessionStorage.findingId;
      this.username = sessionStorage.name;
      this.userId = sessionStorage.uid;
      api.get('/finding/'+findingId).then((response)=> {
        this.finding = response.data;
        if(this.finding.Base){
          api.get('/finding/'+this.finding.Base).then((response)=> {
            this.parentFinding = response.data;
            console.log(this.parentFinding);
          });
        }
      });
     
      api.get('/comment/finding/'+findingId).then((response) => {
        this.comments = response.data;
      });
      api.get('/finding/replication/'+findingId).then((response)=>{
        this.replications = response.data;
        this.calculateSimilarities();
      });
      api.get('/finding/variation/'+findingId).then((response)=>{
        this.variations = response.data;
      });
    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      enableCommentInput(type){
          this.isCommentInputEnabled = true;
          this.commentType = type;
      },
      postComment(){
        const url = '/comment/';
        const comment = {
          userId : sessionStorage.uid,
          findingId: sessionStorage.findingId,
          value: this.commentValue,
          type: this.commentType
        }
        const config = {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        api.post(url, qs.stringify(comment),config).then((response)=>{
          var savedComment = response.data;
          if(!savedComment.UserName){savedComment.UserName = sessionStorage.name;} // fix this from backend and remove this line;
          this.isCommentInputEnabled = false;
          this.commentType = 0;
          this.commentValue = "";
          this.comments.splice(0,0,response.data);
        });
      },
      getMedia(finding){
        if(finding.Dataset2.length<this.datasetIndex){
          return "default-finding-image.jpg";
        }
        if(!finding.Dataset2[this.datasetIndex]){
          return "default-finding-image.jpg";
        }
        if(finding.Dataset2[this.datasetIndex].Media == null || finding.Dataset2[this.datasetIndex].Media == ""){
          return "default-finding-image.jpg";
        }
        return finding.Dataset2[this.datasetIndex].Media+'.jpg';
      },
      replicateOrRemix(){
        var newFinding = this.finding;
        if(this.selectedTab==1){
          // set type
          newFinding.Type = 2;
        } else {
          // set type
          newFinding.Type = 3;
          newFinding.Dataset2.Condition = "";
        }
        newFinding.Dataset2.Uv = 0;
        newFinding.Dataset2.Lux = 0;
        newFinding.Dataset2.Media = "";
        newFinding.Base = this.finding.Id;
        newFinding.Id = null;
        newFinding.UserId = sessionStorage.uid;
       // set base
        var nf = qs.stringify(newFinding);
        this.$store.dispatch('setFindingData', nf);
        this.$router.push({path: '/find_ar'});
      },
      calculateSimilarities(){
        this.similarResults = 0;
        this.differentResults = 0;
        this.notsureResults = 0;
        this.replications.forEach((r)=>{
          if(r.Similarity == 1){
            this.similarResults++;
          } else if(r.Similarity == 2){
            this.differentResults++;
          } else {
            this.notsureResults++;
          }
        })
      },
      loadFinding(findingId){
        console.log("load finding...");
        this.$store.dispatch('setFindingIdData', findingId);
        window.location.reload(); //find the vue method to reload component
      },
      setDatasetIndex(value){
        if(this.finding.Dataset2.length==0){return;}
        if(this.datasetIndex+value<0){
          return;
        }
        if(this.datasetIndex+value == this.finding.Dataset2.length){
          return;
        }
        this.datasetIndex += value;
        this.reload();
      },
      reload() {
        this.$forceUpdate();
      },
      jump(url) {
        this.$router.push({path: '/find_ar'})
      },
    },

  }
</script>

<style lang="scss">
  @font-face{
    font-family: 'roboto-regular';
    src: url('../assets/fonts/Roboto-Regular.ttf');
  }
  body {
    font-family: "roboto-regular";
  }
  .finding_wrap {
    padding-right: 30px;
    // margin: 90px 160px;
    p {
        font-size: 20px;
        font-weight: 600;
      }
  }
  .finding_content {
    display: inline-block;
    width: 60%;
    text-align: left; 
    i {
      font-size: 22px;
      font-weight: 600;
    }
    img {
      width: 100%;
      height: auto;
    }
    .finding_block {
      position: relative;
      margin-right: 30px;
      margin-bottom: 20px;
      background-color: #E4EEEF;
      border-radius: 5px;
      .finding-title{
        background-color: white;
        #finding-name {
          margin:0;
          padding: 20px;
          padding-left: 0;
          padding-bottom: 10px;
          color: #fa5708;
          font-size: 45px;
        }
        #finding-description{
          margin:0;
          padding: 0 20px 20px 0;
          font-weight: normal;
        }
      }
      .notes-section{
        display: block;
        width: 100%;
        min-height: 100px;
        margin-top: 0;
        border-radius: 0 0 10px 10px;
        #finding-time{
          margin: 0;
          padding: 20px;
          padding-bottom: 10px;
          color: #005666;
          font-size: 22px;
          font-weight: normal;
        }
        #finding-note{
          padding: 0 20px 20px 20px;
          margin: 0;
          font-size: 26px;
          font-weight: normal;
        }
        
      }
    }
    #finding_comment {
      margin-right: 30px;
      padding: 30px;
      padding-top: 10px;
      background-color: #F3F5F5;
      border-radius: 5px;
      .comment_row {
        background-color: white;
        margin-bottom: 15px;
        padding: 16px;
        border-radius: 5px;
        .comment-user{
          font-size: 20px;
        }
        .comment-value{
          font-size: 26px;
        }
        button {
          background-color: #FF5B24;
          color: white;
          font-size: 16px;
          height: 28px;
          width: auto;
          padding-left: 10px;
          padding-right: 10px;
          margin-left: 10px;
          vertical-align: top;
          border-radius: 20px !important;
          border: 1px solid orange;
          outline: none;
          cursor: pointer;
        }
        i {
          color: grey;
        }
        img {
          width: 24px;
          height: 24px;
          margin-right: 10px;
        }
        span {
          vertical-align: middle;
        }
      }
      p {
        font-size: 26px;
      }
      input {
        font-size: 24px;
        width: 68%;
        height: 24px;
        vertical-align: top;
        display: inline-block;
        border-radius: 20px !important;
        border: 1px solid orange;
        outline: none;
        padding-left: 20px;
        font-size: 22px;
      }
    }
  }
  #finding-name{
    margin-top: 20px;
    font-size: 33px;
  }
  #finding-username{
    font-size: 24px;
  }
  #finding-description{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 25px;
  }
  .middle-column{
     display: inline-block;
    width: 10%;
  }
  .finding_list {
    display: inline-block;
    width: 30%;
    // margin-right: 10px;
    vertical-align: top;
    text-align: center;
  }
  .similar-block {
    width: 32%; 
    height: 80px; 
    background-color: #E3F6F5; 
    margin-top: 10px;
    margin-bottom: 10px; 
    padding-top: 10px;
    padding-bottom: 10px;
    display: inline-block; 
    vertical-align: top;
    font-size: 18px;
    font-weight: bold;
    color: #12333d;
    .similar-icon{
     font-size: 38px;
    }
    .similar-value{
      display: inline;
      color: #12333d;
      font-size: 45px;
      font-weight: bold;
    }
    .similar-label{
      font-size: 25px;
    }
  }
  .replication-remix-tab-group {
    border: 1px solid #005666;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .tab-selection {
    width: 50%; 
    height: 32px; 
    display: inline-block; 
    vertical-align: top;
    font-size: 24px;
    color: #005666;
    background-color: white;
    padding-top: 8px
  }
  .selected-tab {
    font-weight:bold;
    color:white;
    background-color: #005666;
  }
  .finding-tab {
    font-size: 24px;
    cursor: pointer;
  }
  .finding-tab:hover{
    font-weight: bold;
  }
  .replicate-remix-button{
    padding-top: 10px;
    width: 100%; 
    height: 40px; 
    background-color: #fa5708; 
    color: white;
    font-size: 26px;
    cursor: pointer;
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 20px;
    border-radius: 5px;
  }
  .finding-header{
    text-align: left;
    p {
      font-size: 20px;
      padding: 5px;
      margin: 0 !important;
    }
  }
  .clickable{
    cursor: pointer;
  }
  .back-button{
    font-size: 2rem;
    padding: 0;
    width: 10px;
    border: none !important;
    outline: none !important;
  }
  .el-icon-user-solid{
    font-size: 1.5rem;
    border-radius: 50%;
    background-color: lightgray;
    padding: 5px;
    margin-right: 10px;
  }
  .comment-icon{
    font-size: 2rem !important;
    margin-right: 10px;
  }
  .default-card {
    width: 100% !important;
    margin-left: 0 !important;
  }
  .selected-comment {
    filter: saturate(1) !important;
  }
  .greyout {
    filter: saturate(0);
  }
  .empty-list-image-block {
    width: 100%;
    height:auto;
    text-align: center;
    margin-bottom: 30px !important;
    p {
      color: red;
      font-size: 26px;
    }
  }
  .empty-list-image {
    width: 50%;
    height: auto;
    margin-top: 20px;
  }
</style>